<template>
	<view class="test-container">
		<view class="test-header">
			<text class="test-title">用户系统测试</text>
		</view>
		
		<!-- 登录状态显示 -->
		<view class="status-section">
			<view class="status-title">当前状态</view>
			<view class="status-info">
				<text>登录状态: {{ isLoggedIn ? '已登录' : '未登录' }}</text>
				<text v-if="userInfo">用户: {{ userInfo.username }}</text>
				<text v-if="userToken">Token: {{ userToken.substring(0, 10) }}...</text>
			</view>
		</view>
		
		<!-- 用户认证测试 -->
		<view class="test-section">
			<view class="section-title">用户认证测试</view>
			
			<view class="test-item">
				<button @click="testRegister" :disabled="loading">测试注册</button>
				<text class="test-result">{{ registerResult }}</text>
			</view>
			
			<view class="test-item">
				<button @click="testLogin" :disabled="loading">测试登录</button>
				<text class="test-result">{{ loginResult }}</text>
			</view>
			
			<view class="test-item">
				<button @click="testLogout" :disabled="loading">测试登出</button>
				<text class="test-result">{{ logoutResult }}</text>
			</view>
		</view>
		
		<!-- 购物车测试 -->
		<view class="test-section">
			<view class="section-title">购物车测试</view>
			
			<view class="test-item">
				<button @click="testAddToCart" :disabled="loading || !isLoggedIn">添加商品到购物车</button>
				<text class="test-result">{{ addCartResult }}</text>
			</view>
			
			<view class="test-item">
				<button @click="testGetCart" :disabled="loading || !isLoggedIn">获取购物车</button>
				<text class="test-result">{{ getCartResult }}</text>
			</view>
			
			<view class="test-item">
				<button @click="testClearCart" :disabled="loading || !isLoggedIn">清空购物车</button>
				<text class="test-result">{{ clearCartResult }}</text>
			</view>
		</view>
		
		<!-- 购物车内容显示 -->
		<view class="cart-section" v-if="cartItems.length > 0">
			<view class="section-title">购物车内容</view>
			<view class="cart-item" v-for="item in cartItems" :key="item._id">
				<text>{{ item.productInfo.name }} x {{ item.quantity }}</text>
				<text>¥{{ item.productInfo.price }}</text>
			</view>
		</view>
		
		<!-- 操作按钮 -->
		<view class="action-section">
			<button @click="clearAllResults">清空结果</button>
			<button @click="goToLogin">去登录页面</button>
			<button @click="goToCart">去购物车页面</button>
		</view>
	</view>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'

// 响应式数据
const loading = ref(false)
const userToken = ref('')
const userInfo = ref(null)
const cartItems = ref([])

// 测试结果
const registerResult = ref('')
const loginResult = ref('')
const logoutResult = ref('')
const addCartResult = ref('')
const getCartResult = ref('')
const clearCartResult = ref('')

// 计算属性
const isLoggedIn = computed(() => !!userToken.value)

// 检查登录状态
const checkLoginStatus = () => {
	userToken.value = uni.getStorageSync('userToken') || ''
	userInfo.value = uni.getStorageSync('userInfo') || null
}

// 测试注册
const testRegister = async () => {
	loading.value = true
	registerResult.value = '注册中...'
	
	try {
		const timestamp = Date.now()
		const result = await uniCloud.callFunction({
			name: 'user-auth',
			data: {
				action: 'register',
				username: `test_${timestamp}`,
				password: '123456',
				nickname: `测试用户_${timestamp}`
			}
		})
		
		registerResult.value = `注册结果: ${result.result.errCode === 0 ? '成功' : result.result.errMsg}`
	} catch (error) {
		registerResult.value = `注册失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 测试登录
const testLogin = async () => {
	loading.value = true
	loginResult.value = '登录中...'
	
	try {
		const result = await uniCloud.callFunction({
			name: 'user-auth',
			data: {
				action: 'login',
				username: 'testuser',
				password: '123456'
			}
		})
		
		console.log('测试登录云函数返回结果:', result.result)
		
		if (result.result.errCode === 0) {
			// 根据实际返回结构保存数据
			uni.setStorageSync('userToken', result.result.token)
			if (result.result.userInfo) {
				uni.setStorageSync('userInfo', result.result.userInfo)
			} else {
				// 如果没有userInfo，创建基本用户信息
				uni.setStorageSync('userInfo', {
					username: 'testuser',
					userId: result.result.userId || ''
				})
			}
			checkLoginStatus()
			loginResult.value = '登录成功'
		} else {
			loginResult.value = `登录失败: ${result.result.errMsg}`
		}
	} catch (error) {
		loginResult.value = `登录失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 测试登出
const testLogout = async () => {
	loading.value = true
	logoutResult.value = '登出中...'
	
	try {
		const result = await uniCloud.callFunction({
			name: 'user-auth',
			data: {
				action: 'logout',
				token: userToken.value
			}
		})
		
		uni.removeStorageSync('userToken')
		uni.removeStorageSync('userInfo')
		checkLoginStatus()
		cartItems.value = []
		logoutResult.value = '登出成功'
	} catch (error) {
		logoutResult.value = `登出失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 测试添加到购物车
const testAddToCart = async () => {
	loading.value = true
	addCartResult.value = '添加中...'
	
	try {
		const result = await uniCloud.callFunction({
			name: 'user-cart',
			data: {
				action: 'addToCart',
				token: userToken.value,
				productId: 1,
				quantity: 1,
				productInfo: {
					name: '测试商品',
					price: 99,
					image: '/static/test.jpg',
					specs: '测试规格'
				}
			}
		})
		
		addCartResult.value = `添加结果: ${result.result.errCode === 0 ? '成功' : result.result.errMsg}`
		if (result.result.errCode === 0) {
			await testGetCart()
		}
	} catch (error) {
		addCartResult.value = `添加失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 测试获取购物车
const testGetCart = async () => {
	loading.value = true
	getCartResult.value = '获取中...'
	
	try {
		const result = await uniCloud.callFunction({
			name: 'user-cart',
			data: {
				action: 'getCart',
				token: userToken.value
			}
		})
		
		if (result.result.errCode === 0) {
			cartItems.value = result.result.data || []
			getCartResult.value = `获取成功，共${cartItems.value.length}件商品`
		} else {
			getCartResult.value = `获取失败: ${result.result.errMsg}`
		}
	} catch (error) {
		getCartResult.value = `获取失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 测试清空购物车
const testClearCart = async () => {
	loading.value = true
	clearCartResult.value = '清空中...'
	
	try {
		const result = await uniCloud.callFunction({
			name: 'user-cart',
			data: {
				action: 'clearCart',
				token: userToken.value
			}
		})
		
		clearCartResult.value = `清空结果: ${result.result.errCode === 0 ? '成功' : result.result.errMsg}`
		if (result.result.errCode === 0) {
			cartItems.value = []
		}
	} catch (error) {
		clearCartResult.value = `清空失败: ${error.message}`
	} finally {
		loading.value = false
	}
}

// 清空所有结果
const clearAllResults = () => {
	registerResult.value = ''
	loginResult.value = ''
	logoutResult.value = ''
	addCartResult.value = ''
	getCartResult.value = ''
	clearCartResult.value = ''
}

// 跳转到登录页面
const goToLogin = () => {
	uni.navigateTo({
		url: '/pages/login/login'
	})
}

// 跳转到购物车页面
const goToCart = () => {
	uni.navigateTo({
		url: '/pages/shopping/cart/cart'
	})
}

// 页面加载时检查登录状态
onMounted(() => {
	checkLoginStatus()
	if (isLoggedIn.value) {
		testGetCart()
	}
})
</script>

<style scoped>
.test-container {
	padding: 40rpx;
	background-color: #f5f5f5;
	min-height: 100vh;
}

.test-header {
	text-align: center;
	margin-bottom: 40rpx;
}

.test-title {
	font-size: 48rpx;
	font-weight: bold;
	color: #333;
}

.status-section, .test-section, .cart-section, .action-section {
	background: white;
	border-radius: 20rpx;
	padding: 30rpx;
	margin-bottom: 30rpx;
}

.status-title, .section-title {
	font-size: 36rpx;
	font-weight: bold;
	color: #333;
	margin-bottom: 20rpx;
}

.status-info text {
	display: block;
	margin-bottom: 10rpx;
	color: #666;
}

.test-item {
	display: flex;
	align-items: center;
	margin-bottom: 20rpx;
	padding: 20rpx;
	background: #f8f8f8;
	border-radius: 10rpx;
}

.test-item button {
	width: 300rpx;
	height: 70rpx;
	background: #007aff;
	color: white;
	border: none;
	border-radius: 10rpx;
	font-size: 28rpx;
	margin-right: 20rpx;
}

.test-item button:disabled {
	background: #ccc;
}

.test-result {
	flex: 1;
	color: #666;
	font-size: 26rpx;
}

.cart-item {
	display: flex;
	justify-content: space-between;
	padding: 20rpx;
	background: #f8f8f8;
	border-radius: 10rpx;
	margin-bottom: 10rpx;
}

.action-section {
	display: flex;
	justify-content: space-around;
}

.action-section button {
	width: 200rpx;
	height: 70rpx;
	background: #28a745;
	color: white;
	border: none;
	border-radius: 10rpx;
	font-size: 28rpx;
}
</style> 